<template>
  <div>
    <p>
      <el-input style="width: 200px; margin-right: 10px;" v-model="mobile" placeholder=""></el-input>
      <button v-on:click="list(1)" class="btn btn-white btn-default btn-round" style="margin-right: 10px;">
        
      </button>
      <button class="btn btn-white btn-default btn-round" @click="addMember" style="margin-right: 10px;"></button>
      <button class="btn btn-white btn-default btn-round" @click="exportMember" style="margin-right: 10px;"></button>
      <span></span>
    </p>

    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      width="30%">
      <el-form ref="form" :model="form" :rules="rules" label-width="100px">
        <el-form-item label="" prop="mobile">
          <el-input v-model="form.mobile" size="small"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" size="small"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="form.password" size="small"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
          <el-radio-group v-model="form.role" size="small">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
<!--        <el-form-item label="" prop="payStatus">-->
<!--          <el-radio-group v-model="form.payStatus" size="mini">-->
<!--            <el-radio :label="0"></el-radio>-->
<!--            <el-radio :label="1"></el-radio>-->
<!--          </el-radio-group>-->
<!--        </el-form-item>-->
        <el-form-item label="" prop="doQuestion">
          <el-radio-group v-model="form.doQuestion" size="mini">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="deviceLimitNum">
          <el-input-number
            size="small"
            v-model="form.deviceLimitNum"
            :min="1" :max="10"
          ></el-input-number>
        </el-form-item>
<!--        <el-form-item label="" prop="phone">-->
<!--          <el-input-->
<!--            readonly-->
<!--            v-model="form.phone"-->
<!--            size="small">-->
<!--          </el-input>-->
<!--        </el-form-item>-->
        <el-form-item label="" prop="userType">
          <el-radio-group v-model="form.userType" size="mini">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="choice">
          <el-select
            class="mySelect"
            style="width: 100%"
            v-model="form.choice"
            placeholder="">
            <el-option
              v-for="item in chapterOption"
              :key="item.id"
              :label="item.title"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="licenseType">
          <el-radio-group v-model="form.licenseType" size="small">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="loginType">
          <el-radio-group v-model="form.loginType" size="small">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2">PC</el-radio>
            <el-radio :label="3"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onCancel"> </el-button>
        <el-button type="primary" @click="onSubmit"> </el-button>
      </span>
    </el-dialog>
    <el-table
      stripe
      border
      :data="members"
      style="width: 100%">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="mobile" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{ scope.row.phone || '' }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.role === 1" type="info"></el-tag>
          <el-tag size="mini" v-if="scope.row.role === 2"></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.payStatus === 1"></el-tag>
          <el-tag size="mini" v-else-if="scope.row.payStatus === 0" type="info"></el-tag>
          <el-tag size="mini" v-else type="info"></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.doQuestion === 1"></el-tag>
          <el-tag size="mini" v-else-if="scope.row.doQuestion === 0" type="info"></el-tag>
          <el-tag size="mini" v-else type="info"></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="title" label="" width="120"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.userType === 1"></el-tag>
          <el-tag size="mini" v-else-if="scope.row.userType === 0" type="info"></el-tag>
          <el-tag size="mini" v-else type="info"></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="deviceLimitNum" label=""></el-table-column>
      <el-table-column prop="ip" label="IP" width="140"></el-table-column>
      <el-table-column prop="registerTime" label="" width="160"></el-table-column>
      <el-table-column label="" width="220">
        <template slot-scope="scope">
          <el-button
            @click="toViewingRecord(scope.row)"
            type="text"
            size="small">
            
          </el-button>
          <el-button
            @click="toCheckDevice(scope.row)"
            type="text"
            size="small">
            
          </el-button>
          <el-button
            @click="toDeleteUser(scope.row)"
            type="text"
            size="small">
            
          </el-button>
          <el-button
            @click="toEditUser(scope.row)"
            type="text"
            size="small">
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination-contain">
      <pagination
        ref="pagination"
        v-bind:list="list"
        v-bind:itemCount="8">
      </pagination>
    </div>

    <el-dialog
      title=""
      :visible.sync="deviceDialogVisible"
      width="800px">
      <el-table :data="deviceContent" style="width: 100%">
        <el-table-column prop="id" label="ID"></el-table-column>
        <el-table-column prop="memberId" label="ID"></el-table-column>
        <el-table-column prop="deviceId" label="ID" width="300"></el-table-column>
        <el-table-column label="" width="160">
          <template slot-scope="scope">
            <span>{{ deviceTypeOption[scope.row.deviceType] }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
            <el-button @click="handleDelete(scope.row)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deviceDialogVisible = false;"> </el-button>
        <el-button type="primary" @click="deviceDialogVisible = false;"> </el-button>
      </span>
    </el-dialog>

    <el-dialog
      title=""
      :visible.sync="viewingRecordDialogVisible"
      width="800px">
      <el-table :data="viewingRecordList" max-height="600" style="width: 100%">
        <el-table-column
          width="500"
          prop="courseName"
          label="">
        </el-table-column>
        <el-table-column
          prop="creatorTime"
          label="">
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="viewingRecordDialogVisible = false;"> </el-button>
        <el-button type="primary" @click="viewingRecordDialogVisible = false;"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
  import Pagination from "../../components/pagination";
  export default {
    components: {Pagination},
    name: "business-member",
    data: function() {
      return {
        mobile: '',  //
        member: {},
        members: [], //tableData
        dialogVisible: false,
        dialogTitle: "",
        form: {
          id: "",
          mobile: "",        //
          name: "",          //
          password: "",      //
          role: 1,           //
          payStatus: 0,      //
          doQuestion: 1,     //
          deviceLimitNum: 2, //
          phone: '',         //
          userType: 0,      //
          choice: '',        //
          licenseType: 3,    //
          loginType: 1,      //
        },
        rules: {
          mobile: [{ required: true, message: '', trigger: 'blur' }],
          name: [{ required: true, message: '', trigger: 'blur' }],
          password: [{ required: true, message: '', trigger: 'blur' }],
        },
        deviceContent: [],
        deviceTypeOption: {
          "1": 'android',
          "2": 'ios',
          "3": 'windows',
          "4": 'h5',
        },
        deviceDialogVisible: false,
        chapterOption: [],

        viewingRecordDialogVisible: false,
        viewingRecordList: [],
      }
    },
    mounted: function() {
      let _this = this;
      _this.$refs.pagination.size = 15;
      _this.list(1);
      _this.getChapterOption()
      // sidebar
      // this.$parent.activeSidebar("business-member-sidebar");

    },
    methods: {
      getChapterOption() {
        let _this = this;
        _this.$ajax.post(process.env.VUE_APP_SERVER + '/dev-api/business/admin/section/list', {
          "page":1,
          "size":1500,
          "courseId":"TYAIILon",
          "chapterId":"NmmbLYYl"
        }).then((response)=>{
          const { code, content } = response.data;
          _this.chapterOption = content.list;
        })
      },
      /**
       * 
       */
      list(page) {
        let _this = this;
        Loading.show();
        _this.$ajax.post(process.env.VUE_APP_SERVER + '/dev-api/business/admin/member/list', {
          page: page,
          size: _this.$refs.pagination.size,
          mobile: this.mobile,
        }).then((response)=>{
          Loading.hide();
          let resp = response.data;
          _this.members = resp.content.list;
          _this.$refs.pagination.render(page, resp.content.total);

        })
      },
      exportMember() {
        this.$ajax.post(process.env.VUE_APP_SERVER + '/dev-api/business/admin/member/export', {}, {
          headers: {
            'Content-Type': 'application/json; application/octet-stream'
          },
          responseType: 'arraybuffer'
        }).then((res)=>{
          const aLink = document.createElement('a')
          var blob = new Blob([res.data], { type: "application/vnd.ms-excel;charset=UTF-8" })
          var fileName = ""
          aLink.href = URL.createObjectURL(blob)
          aLink.setAttribute('download', fileName) // 
          document.body.appendChild(aLink)
          aLink.click()
          document.body.appendChild(aLink)
        })
      },
      addMember() {
        this.form = {
          id: "",
          mobile: "",        //
          name: "",          //
          password: "",      //
          role: 1,           //
          payStatus: 0,      //
          doQuestion: 1,     //
          deviceLimitNum: 2, //
          phone: '',         //
          userType: 0,       //
          choice: '',        //
          licenseType: 3,    //
          loginType: 1,      //
        }
        this.dialogVisible = true
        this.dialogTitle = ""
      },
      onCancel() {
        this.dialogVisible = false;
        this.$refs['form'].resetFields();
      },
      onSubmit() {
        this.$refs['form'].validate((valid) => {
          if (valid) {
            const params = this.form;
            this.$ajax.post(process.env.VUE_APP_SERVER + '/dev-api/business/admin/member/save', params).then((response)=>{
              const {success, message} = response.data;
              if(success) {
                this.list(1);
                this.onCancel();
                this.$message({
                  type: 'success',
                  message: ''
                });
              }else {
                this.$message({
                  type: 'error',
                  message: message
                });
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      toEditUser(data) {
        const { id, mobile, name, password, role, payStatus, doQuestion, deviceLimitNum, phone, userType, choice, licenseType, loginType } = data;
        this.form = { id, mobile, name, password, role, payStatus, doQuestion, deviceLimitNum, phone, userType, choice, licenseType, loginType };
        this.dialogVisible = true;
        this.dialogTitle = ""
      },
      toDeleteUser(data) {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^HATI-HATI$/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          const { id } = data;
          this.$ajax.delete(process.env.VUE_APP_SERVER + '/dev-api/business/admin/member/delete/' + id).then((response) => {
            this.$refs.pagination.size = 15;
            this.list(1);
            this.$message({
              type: 'success',
              message: '!'
            });
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      toCheckDevice(data) {
        const { id } = data;
        this.deviceContent = [];
        this.$ajax.get(process.env.VUE_APP_SERVER + '/dev-api/business/admin/member/device-list/' + id).then((response)=>{
          const { data } = response;
          const { content } = data;
          this.deviceContent = content;
          this.deviceDialogVisible = true;
        })
      },
      handleDelete(data) {
        const { id } = data;
        this.deviceContent = this.deviceContent.filter(item => item.id !== id)
        this.$ajax.delete(process.env.VUE_APP_SERVER + '/dev-api/business/admin/member/device-delete/' + id);
      },
      toViewingRecord(data) {
        const { id } = data;
        this.$ajax.get(process.env.VUE_APP_SERVER + '/dev-api/business/admin/member/watchHistory?memberId=' + id).then((response)=>{
          const { data } = response;
          const { content } = data;
          this.viewingRecordList = content;
          this.viewingRecordDialogVisible = true;
        })
      }
    }
  }
</script>
<style>
  .mySelect .el-input--suffix .el-input__inner {
    background: #ffffff !important;
  }
  .pagination-contain {
    margin-top: 20px;
    display: flex;
    justify-content: end;
  }
</style>
